.mg-root {
  min-height: 100vh;
  background: transparent;
  color: var(--color-text, #1f2a44);
  font-family: var(--font-base, "Segoe UI", "Helvetica Neue", Arial, sans-serif);
}

.mg-root .mg-background {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: var(--space-lg, 24px);
}

.mg-root .mg-shell {
  width: min(1080px, 100%);
  background: var(--color-panel, #ffffff);
  border: 1px solid var(--color-border, #a4c4f5);
  border-radius: var(--radius-lg, 18px);
  box-shadow: var(--shadow-soft, 0 12px 38px rgba(73, 104, 170, 0.15));
  padding: var(--space-xl, 32px);
  display: flex;
  flex-direction: column;
  gap: var(--space-lg, 22px);
}

.mg-root .mg-header {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm, 10px);
}

.mg-root .mg-home-link {
  align-self: flex-start;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: var(--radius-md, 14px);
  text-decoration: none;
  color: var(--color-text, #1f2a44);
  border: 1px solid var(--color-border, #a4c4f5);
  background: var(--color-surface, #bed6ff);
  font-weight: 700;
  transition: transform 120ms ease, box-shadow 120ms ease;
}

.mg-root .mg-home-link:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 18px rgba(73, 104, 170, 0.15);
}

.mg-root .mg-title-block {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs, 6px);
}

.mg-root .mg-eyebrow {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 0.8rem;
  color: var(--color-muted, #4b5563);
}

.mg-root .mg-title {
  margin: 0;
  font-size: clamp(26px, 3vw + 8px, 40px);
  font-weight: var(--font-weight-strong, 700);
}

.mg-root .mg-subtitle {
  margin: 0;
  color: var(--color-muted, #4b5563);
  max-width: 720px;
  line-height: 1.6;
}

@media (max-width: 640px) {
  .mg-root .mg-background {
    padding: var(--space-md, 16px);
  }

  .mg-root .mg-shell {
    padding: var(--space-lg, 22px);
  }

  .mg-root .mg-home-link {
    font-size: 0.95rem;
  }

  .mg-root .mg-title {
    font-size: clamp(24px, 5vw + 6px, 32px);
  }
}
