.mg-root .mg-game {
  display: flex;
  flex-direction: column;
  gap: var(--space-md, 16px);
}

.mg-root .mg-toolbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  gap: var(--space-md, 16px);
  padding: var(--space-md, 16px);
  background: var(--color-surface, #bed6ff);
  border: 1px solid var(--color-border, #a4c4f5);
  border-radius: var(--radius-md, 14px);
  box-shadow: var(--shadow-soft, 0 12px 38px rgba(73, 104, 170, 0.12));
}

.mg-root .mg-controls {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm, 10px);
}

.mg-root .mg-label {
  font-weight: 700;
  letter-spacing: 0.02em;
  color: var(--color-text, #1f2a44);
}

.mg-root .mg-size-options {
  display: flex;
  gap: var(--space-xs, 6px);
  flex-wrap: wrap;
}

.mg-root .mg-size-btn {
  width: 44px;
  height: 44px;
  border-radius: var(--radius-md, 14px);
  border: 1px solid var(--color-border, #a4c4f5);
  background: var(--color-panel, #ffffff);
  color: var(--color-text, #1f2a44);
  font-weight: 700;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease, background-color 120ms ease;
}

.mg-root .mg-size-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(73, 104, 170, 0.16);
}

.mg-root .mg-size-btn.active {
  background: linear-gradient(135deg, var(--color-accent, #7ea8f8), #97bafc);
  border-color: #88aefb;
  color: #0f172a;
}

.mg-root .mg-score {
  min-width: 160px;
  background: var(--color-panel, #ffffff);
  border: 1px solid var(--color-border, #a4c4f5);
  border-radius: var(--radius-md, 14px);
  padding: var(--space-sm, 10px) var(--space-md, 16px);
  display: flex;
  flex-direction: column;
  gap: 4px;
  box-shadow: var(--shadow-soft, 0 12px 38px rgba(73, 104, 170, 0.12));
}

.mg-root .mg-score-label {
  font-size: 0.9rem;
  color: var(--color-muted, #4b5563);
}

.mg-root .mg-score-value {
  font-size: 1.9rem;
  font-weight: var(--font-weight-strong, 700);
  line-height: 1.1;
}

.mg-root .mg-score-subtext {
  font-size: 0.95rem;
  color: var(--color-muted, #4b5563);
}

.mg-root .mg-placeholder {
  display: grid;
  place-items: center;
  padding: var(--space-xl, 32px);
  border: 1px dashed var(--color-border, #a4c4f5);
  border-radius: var(--radius-md, 14px);
  background: rgba(255, 255, 255, 0.6);
  color: var(--color-muted, #4b5563);
  text-align: center;
}

.mg-root .mg-grid {
  display: grid;
  gap: var(--space-sm, 10px);
  justify-content: center;
  max-width: 760px;
  margin: 0 auto;
}

.mg-root .mg-grid[data-size='S'],
.mg-root .mg-grid[data-size='M'] {
  grid-template-columns: repeat(4, minmax(96px, 1fr));
}

.mg-root .mg-grid[data-size='L'] {
  grid-template-columns: repeat(4, minmax(104px, 1fr));
}

.mg-root .mg-grid[data-size='XL'] {
  grid-template-columns: repeat(5, minmax(96px, 1fr));
}

.mg-root .mg-card {
  position: relative;
  height: 120px;
  border: none;
  border-radius: var(--radius-md, 14px);
  background: var(--color-surface, #bed6ff);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 26px rgba(73, 104, 170, 0.18);
  transition: transform 160ms ease, box-shadow 160ms ease, background-color 160ms ease;
}

.mg-root .mg-card:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 14px 32px rgba(73, 104, 170, 0.22);
}

.mg-root .mg-card.is-flipped {
  background: #ffffff;
}

.mg-root .mg-card.is-matched {
  background: linear-gradient(135deg, var(--color-accent, #7ea8f8), #b3c9fb);
  box-shadow: 0 12px 30px rgba(126, 168, 248, 0.38);
}

.mg-root .mg-card:disabled {
  cursor: not-allowed;
  opacity: 0.94;
}

.mg-root .mg-card-face {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.mg-root .mg-card-face img {
  width: 72%;
  height: 72%;
  object-fit: contain;
}

.mg-root .mg-card-back-symbol {
  font-size: 1.4rem;
  color: var(--color-text, #1f2a44);
  opacity: 0.7;
}

.mg-root .mg-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.36);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-md, 16px);
  z-index: 30;
}

.mg-root .mg-modal {
  width: min(420px, 100%);
  background: var(--color-panel, #ffffff);
  border: 1px solid var(--color-border, #a4c4f5);
  border-radius: var(--radius-lg, 18px);
  box-shadow: 0 16px 44px rgba(73, 104, 170, 0.28);
  padding: var(--space-xl, 32px);
  display: flex;
  flex-direction: column;
  gap: var(--space-sm, 10px);
}

.mg-root .mg-modal-title {
  margin: 0;
  font-size: 1.4rem;
}

.mg-root .mg-modal-text {
  margin: 0;
  color: var(--color-muted, #4b5563);
}

.mg-root .mg-modal-actions {
  margin-top: var(--space-sm, 10px);
  display: flex;
  gap: var(--space-sm, 10px);
  flex-wrap: wrap;
}

.mg-root .mg-primary-btn,
.mg-root .mg-ghost-btn {
  border-radius: var(--radius-md, 14px);
  padding: 10px 16px;
  border: 1px solid var(--color-border, #a4c4f5);
  font-weight: 700;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease, background-color 120ms ease;
}

.mg-root .mg-primary-btn {
  background: linear-gradient(135deg, var(--color-accent, #7ea8f8), #97bafc);
  color: #0f172a;
}

.mg-root .mg-primary-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 22px rgba(126, 168, 248, 0.35);
}

.mg-root .mg-ghost-btn {
  background: var(--color-panel, #ffffff);
  color: var(--color-text, #1f2a44);
}

.mg-root .mg-ghost-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 18px rgba(73, 104, 170, 0.16);
}

@media (max-width: 900px) {
  .mg-root .mg-grid[data-size='S'],
  .mg-root .mg-grid[data-size='M'],
  .mg-root .mg-grid[data-size='L'],
  .mg-root .mg-grid[data-size='XL'] {
    grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
  }
}

@media (max-width: 640px) {
  .mg-root .mg-card {
    height: 96px;
  }

  .mg-root .mg-toolbar {
    align-items: flex-start;
  }
}
